<template>
  <div class="detail" id="notebook-list">
    <header>
      <a href="#" class="btn" @click.prevent="onCreate"
        ><i class="iconfont icon-plus"></i> 新建笔记本</a
      >
    </header>

    <main>
      <div class="layout">
        <h3>笔记本列表({{ noteList.length }})</h3>
        <div class="book-list">
          <router-link
            v-for="note in noteList"
            :to="`/note/?noteId=${note.id}`"
            class="notebook"
            :key="note.id"
          >
            <div>
              <span class="iconfont icon-notebook">{{ note.title }}</span>
              <span>{{ note.noteCounts }}</span>
              <span class="action" @click.stop.prevent="onEdit(note)"
                >编辑</span
              >
              <span class="action" @click.stop.prevent="onDelete(note)"
                >删除</span
              >
              <span class="date"
                >{{ note.friendlyCreatedAt }}创建,{{
                  note.friendlyUpdatedAt
                }}更新</span
              >
            </div>
          </router-link>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
// import Auth from "../apis/auth";
// import List from "@/apis/list";
import { mapState, mapActions, mapGetters } from "vuex";
// import friendlyDate from "@/helpers/util";

export default {
  data() {
    return {};
  },

  computed: {
    ...mapGetters(["noteList"])
  },

  created() {
    this.checkLogin();

    this.getNoteList();
  },

  methods: {
    ...mapActions([
      "getNoteList",
      "createNote",
      "editNote",
      "deleteNote",
      "checkLogin"
    ]),

    onCreate() {
      this.$prompt("请输入新笔记本标题", "我的笔记本", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        inputPattern: /^.{1,30}$/,
        inputErrorMessage: "笔记本标题不为空且不超过30个字符"
      }).then(({ value }) => {
        this.createNote({ title: value });
      });
    },

    onEdit(note) {
      let title = "";

      this.$prompt("修改标题", "", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        inputValue: note.title,
        inputPattern: /^.{1,30}$/,
        inputErrorMessage: "笔记本标题不为空且不超过30个字符"
      }).then(({ value }) => {
        this.editNote({ noteId: note.id, title: value });
      });
    },

    onDelete(note) {
      this.$confirm("确定将笔记扔到回收站?", "删除笔记本", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.deleteNote({ noteId: note.id });
      });
    }
  }
};
</script>

<style lang="less" scoped>
@import url(../assets/css/notebook-list.less);
</style>
